import React from 'react'
import './home.css';
import imgServ1 from '../img/servicios/asesoria.png'
import imgServ2 from '../img/servicios/capacitacion1.png'
import imgServ3 from '../img/servicios/ingenieria1.png'
import imgServ4 from '../img/servicios/integracion.png'
import imgServ5 from '../img/servicios/reparaciones.png'
import {Link} from "react-router-dom";


import logoCarousel from '../img/logos/ARGsinfondo-02(1).png'
import HomeCarousel from './HomeCarousel';
import CarruselReact from './CarruselReact';
import CarruselReactCopy from './CarruselReactCopy.js'

function Home() {

    return (
        <div className='home'>
            <HomeCarousel/>
            <div className='btns_home'>
            <Link to="conocenos"><div className='btn_conocenos'>Concenos</div></Link>
            <Link to="contacto"><div className='btn_cotiza'>Cotiza</div></Link>
            </div>
            <div className='home_content'>
                <div className='logo_carousel'><img src={logoCarousel} alt=''></img></div>
            </div>
           
            <div className='carrusel1'>
                    <CarruselReact/>
                    <div className='img_home_midle'></div>
                    <CarruselReactCopy/>
            </div>
             <div className='servicios_home'>
                 <h2 className='servicios_titulo'>Nuestros servicios</h2>
                 <div className='line'></div>
             <div className='servicios_img'>
                 <div className='serv_img'>
                 <img src={imgServ1}></img>Asesora especializada
                 </div>
                 <div className='serv_img'>
                 <img src={imgServ2}></img>Capacitacin                 </div>
                 <div className='serv_img'>
                 <img src={imgServ3}></img>Ingeniera
                 </div>
                 <div className='serv_img'>
                 <img src={imgServ4}></img>Integracin de equipos
                 </div>
                 <div className='serv_img'>
                 <img src={imgServ5}></img>Reparaciones y preventivos
                 </div>
                 
            </div>
            </div>
        </div>
    )
}

export default Home;




